export default {
  head() {
    return {
      title: 'Δείγματα - Προβολή | Φορέας Διαχείρισης Λίμνης Κερκίνης',
      meta: [
        {
          hid: 'description', name: 'description',
          content: 'Προβολή συγκεκριμένου Δείγματος για τη διαχείρηση δεδομένων των Δειγμάτων για τον Φορέα Διαχείρισης Λίμνης Κερκίνης.'
        }
      ]
    }
  },
  data() {
    return {
      main: {
        title: 'Αναζήτηση Δείγματος με id ' + this.$route.params.id,
        emptyElements: 'Δεν βρέθηκε αποθηκευμένο το δείγμα που επιθυμείτε'
      },
      noElements: null,
      deigma: {
        prwtokola: {},
        deigmaThhlastikwn: {},
        // Use farther deigmata types so and make a v-else-if for each one of them
      },

      loading: null,
      snackbar: false,
      snackbarText: '',
      snackbarColor: '',
    }
  },
  computed: {
    getByIdUrl(){
      return this.$store.getters.getDeigmataByIdUrl
    },
    id(){
      return this.$route.params.id
    }
  },
  mounted() {
    this.restCall(this.id)
  },
  methods: {
    checkForPrevPage(pageNumber) {
      if( pageNumber == 0 ) {
        this.prevPage = false
      } else {
        this.prevPage = true
      }
    },
    restCall(id) {
      this.loading = true
      this.$axios.get(this.getByIdUrl + id)
        .then(response => {
          // console.log(response)
          if( response.data == "" ){
            this.noElements = true
            this.snackbarColor = 'red'
            this.snackbarText = 'Δεν βρέθηκε αποθηκευμένο το δείγμα που επιθυμείτε'
            this.snackbar = true
          } else {
            this.noElements = false
            this.deigma = response.data
          }
          this.loading = false
        }).catch((err) => {
          this.loading = false
          this.snackbarColor = 'red'
          this.snackbarText = 'Υπήρξε κάποιο σφάλμα στην αναζήτηση'
          this.snackbar = true
        })
    },
    goToDeigmaThhlastikwnById(id){
      this.$router.push('/thhlastika/deigma-thhlastikwn/' + id)
    },
    toIdEditPage(id) {
      this.$router.push('/deigmata/' + id + '/edit')
    },
    deleteById() {
      this.$axios.delete(this.getByIdUrl + this.id)
        .then(response => {
          if(response.data == true){
            this.snackbarColor = 'green'
            this.snackbarText = 'Η διαγραφή ολοκληρώθηκε επιτυχώς'
            this.snackbar = true
            this.$router.push('/')
          } else if (response.data == false){
            this.snackbarColor = 'red'
            this.snackbarText = 'Υπήρξε κάποιο σφάλμα κατά την διαγραφή'
            this.snackbar = true
          }
        })
        .catch(err => {
          this.snackbarColor = 'red'
          this.snackbarText = 'Υπήρξε κάποιο σφάλμα κατά την διαγραφή'
          this.snackbar = true
          throw err
        })
    }
  }
}
